ogies de garantie dune vigilance active, ainsi que des mcanismes de projet permettant de\n      sapprocher, dans un cadre donn, de ce qui devrait tre fait, permettra non pas de saffranchir dune rcupration de tout acte de design par des motivations marchandes, mais de garder cet affranchissement comme objectif pour mieux faire.")]), _vm._v(" "), _c('p', [_vm._v("Cest  travers cette rflexion que je souhaite guider ma pratique future,  la fois dans un souci de justesse et dauto-critique, mais surtout pour la confronter aux phnomnes tudis dans ce texte. Le contexte actuel tel que jai pu le dcrire\n      rend cette mise en pratique difficile. Pourtant, ce tissu conomique et idologique nest pas lisse:il comprend des asprits dans lesquelles cette discussion a lieu et dautres o la mise en pratique de cette rflexion est possible.\n      Ces asprits doivent tre protges, mais ne doivent pas tre le seul contexte dexercice de cette rflexion car sa mise en pratique en terrain conquis ne permettra ni de la confronter aux difficults dun contexte qui lui est hostile, ni de la\n      diffuser suffisamment via sa pratique.")]), _vm._v(" "), _c('p', [_vm._v("Que ce soit  lcole ou dans la vie professionnelle, la pratique du design graphique invite  une observation du monde qui nous entoure, et  dvelopper des facults danalyse qui paraissent essentielles  un travail de qualit. Cest justement ces\n      comptences que je voudrais mettre  profit dans ma propre pratique, en envisageant le design graphique comme un outil de dcryptage de lenvironnement dans lequel nous voluons. Ce que nous avons pu entrevoir du pouvoir de limage de par son statut\n      de vecteur idologique de structures dominantes peut tre renvers, pour devenir au contraire un vecteur dmancipation et de prise de recul. videmment, le train ne sarrtera pas de sitt, mais viser son arrt ne pourra quaugmenter nos chances\n      de voir un ralentissement.")]), _vm._v(" "), _c('ul', {
